<template>
<CRow>
    <CCol col="12" class="mb-5">
        <CRow>
            <CCol col="8">
                <h3 class="font-weight-bold text-uppercase mb-1">Routing aplikasi Formulir</h3>
                <p class="text-primary text-sm-left mb-0">{{$options.identitas[0].instansi}}</p>
                <p class="text-primary text-sm-left">{{$options.identitas[0].provinsi}}</p>
            </CCol>
            <CCol>
                <div class="text-right mt-5 mr-5">
                    <router-link :to="{ path:'/settings/routing'}" class="btn btn-primary text-white">Halaman Routing</router-link>
                </div>
            </CCol>
        </CRow>
    </CCol>
    <CCol col="12">
        <div class="wrapperModul p-3">
            <CRow class="justify-content-between">
                <CCol sm="2">
                    <CInput label="Nama Routing" v-model.trim="$v.routing.name.$model" :isValid="$v.routing.name.$dirty ? !$v.routing.name.$error : null" placeholder="Ketik Nama Routing" />
                </CCol>

                <CCol sm="3">
                    <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="routing.active" v-model="routing.active" />
                </CCol>
            </CRow>
            <CRow>
                <CCol col="12">
                    <CButton color="primary" class="text-white" @click="ToDatabase()">{{type}}</CButton>
                </CCol>
            </CRow>
        </div>
        <div class="wrapperModul p-3 mt-3" v-if="$route.params.type === 'edit'">
            <CRow v-if="!steps.length">
                <CCol>
                    <center>
                        <CButton color="info" @click="toTambahSteps(0)" size="sm">
                            <CIcon name="cil-note-add" class="mr-1"></CIcon> Tambah Alur Steps
                        </CButton>
                    </center>
                </CCol>
            </CRow>
            <div v-if="steps.length">
                <CRow v-for="(i, index) in $v.steps.$each.$iter" :key="index">
                    <CCol>
                        <CRow>
                            <CCol sm="4">
                                <CInput label="Nama Steps" v-model.trim="i.name.$model" :isValid="i.name.$dirty ? !i.name.$error : null" placeholder="Ketik Nama Routing" />
                            </CCol>
                            <CCol sm="2">
                                <CSelect label="Step" @change="SortSteps()" :value.sync="i.step.$model" v-model.trim="i.step.$model" :options="optionsSteps"></CSelect>
                            </CCol>

                            <CCol sm="3">
                                <CSwitch class="mr-1 mt-4 pt-1" color="primary" :checked.sync="steps[index].active" v-model="steps[index].active" />
                            </CCol>
                        </CRow>
                    </CCol>
                    <CCol sm="3" class="mt-4">
                        <div class="text-right">
                            <CButton color="light" class="text-danger" @click="toDeleteSteps(index)">
                                <CIcon name="cil-trash" class="mr-0"></CIcon>
                            </CButton>
                        </div>
                    </CCol>
                </CRow>
                <center>
                    <CButton color="primary" size="sm" class="text-white mr-2" @click="ToDatabaseSteps()">
                        <CIcon name="cil-save" class="mr-1"></CIcon> Simpan
                    </CButton>
                    <CButton color="info" @click="toTambahSteps(1)" size="sm">
                        <CIcon name="cil-note-add" class="mr-1"></CIcon> Tambah Alur Steps
                    </CButton>
                </center>
            </div>
        </div>
    </CCol>
    <CElementCover :boundaries="[{ sides: ['top', 'left'], query: '.media-body' }]" v-if="isLoading.show" :opacity="0.8">
        <h6 class="d-inline mr-2">Memuat Data... </h6>
        <CSpinner color="success" />
    </CElementCover>
</CRow>
</template>

<script>
import identitas from '@/containers/_identitas'
import tableta from '@/views/table/table'
import {
    required
} from 'vuelidate/lib/validators'
const axios = require('axios');
export default {
    identitas,
    components: {
        tableta
    },
    data() {
        return {
            routing: {
                routing_id: null,
                name: null,
                active: null,
                steps: []
            },
            routingSteps: {
                routing_step_id: null,
                routing_id: null,
                name: null,
                step: null,
                active: null,
            },
            isLoading: {
                text: "Mohon Tunggu, Sedang Proses Penyimpanan Data",
                show: false,
            },
            steps: [],
            type: this.$route.params.type === 'edit' ? "update" : "insert",
        }
    },
    computed: {
        stepsRouting() {
            var steps = this.steps.length;
            var Validate = {
                $each: {
                    name: {
                        required
                    },
                    step: {
                        required
                    }
                }
            }
            var NotValidate = {
                $each: {
                    name: {},
                    step: {},
                }
            }
            return steps >= 1 ? Validate : NotValidate
        },
        optionsSteps() {
            return this.steps.map((e, index) => {
                return {
                    label: index + 1,
                    value: index + 1
                }
            });
        },
    },
    validations() {
        return {
            routing: {
                name: {
                    required
                },
            },
            steps: this.stepsRouting
        }
    },
    created() {
        this.getData()
    },
    methods: {
        SortSteps() {
            function compare(a, b) {
                if (a.step < b.step)
                    return -1;
                if (a.step > b.step)
                    return 1;
                return 0;
            }

            return this.steps.sort(compare);
        },
        getData() {
            axios.post(this.$options.identitas[0].url + '/routing?type=dataById', {
                    id: this.$route.params.id
                })
                .then((e) => {
                    e.data.code === "404" ? this.$router.push({
                        path: '/pages/404'
                    }) : (this.routing = e.data.data)
                    this.routing.steps.length > 1 ? this.steps = this.routing.steps : this.steps = []

                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        ToDatabase() {
            this.$v.routing.$touch()
            if (!this.$v.$invalid) {
                axios.post(this.$options.identitas[0].url + '/routing?type=' + this.type, {
                        data: this.routing
                    })
                    .then((e) => {
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: e.data.message,
                        });

                        this.$router.push({
                            path: '/settings/routing/formulir/edit/' + e.data.data.routing_id
                        })
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
        ToDatabaseSteps() {
            this.$v.steps.$touch()
            if (!this.$v.steps.$invalid) {
                this.isLoading.show = true
                axios.post(this.$options.identitas[0].url + '/routing/steps?type=insert', {
                        data: this.steps
                    })
                    .then((e) => {
                        this.isLoading.show = false
                        this.getData()
                        this.$notify({
                            group: 'foo',
                            title: 'Information',
                            text: e.data.message,
                        });
                    })
                    .catch(function (error) {
                        // handle error
                        console.log(error);
                    })
            }
        },
        toDeleteSteps(index) {

            if (this.steps[index].routing_step_id === null) {
                this.$delete(this.steps, index)
                var renew = this.steps.map((value, index) => {
                    return {
                        routing_step_id: value.routing_step_id,
                        routing_id: value.routing_id,
                        name: value.name,
                        step: index + 1,
                        active: null,
                        accepted: null,
                        decilined: null,
                    }
                });
                return
            }
            this.isLoading.show = true
            axios.post(this.$options.identitas[0].url + '/routing/steps?type=delete', {
                    id: this.steps[index].routing_step_id
                })
                .then((e) => {
                    this.isLoading.show = false
                    this.getData()
                    this.$notify({
                        group: 'foo',
                        title: 'Information',
                        text: e.data.message,
                    });
                })
                .catch(function (error) {
                    // handle error
                    console.log(error);
                })
        },
        toTambahSteps(index) {
            var length = this.steps.length - 1
            var stepsCount = index === 0 ? (index + 1) : (this.steps[length].step + 1)
            var steps = this.steps.push({
                routing_step_id: null,
                routing_id: this.$route.params.id,
                name: null,
                step: stepsCount,
                active: true,
                accepted: null,
                decilined: null,
            });
        }
    }

}
</script>

<style>
.wrapperModul {
    border: 1px solid #dfdfdf;
    border-radius: 5px;

}

.float {
    position: fixed;
    width: 120px;
    bottom: 80px;
    right: 40px;
    color: #FFF;
    text-align: center;
    box-shadow: 2px 2px 3px #999;
}
</style>
